* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    background-color: rgba(0,0,0,0.1);
}

.menu-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.menu-item {
    margin: 5px;
}

.arrow {
    width: 30%;
    min-width: 50px;
    max-width: 200px;
}

.wrapper {
    display: flex;
}

/* Vertical view */
@media all and (max-width: 600px) {
    .wrapper {
        flex-direction: column;
        align-items: center;
    }

    .arrow {
        width: 15%;
        margin: 60px;
        transform: rotate(0.25turn);
    }

    textarea {
        display: block;
        min-width: 300px;
        min-height: 400px;
    }
}

/* Horizontal view */
@media all and (min-width: 600px) {
    .wrapper {
        justify-content: space-around;
        align-items: flex-end;
        flex-direction: row;
    }

    .arrow {
        transform: rotate(1turn);
        align-self: center;
    }

    textarea {
        display: block;
        width: 400px;
        min-height: 600px;
        resize: horizontal;
    }
}

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
    font-family: sans-serif;
    visibility: hidden;
    word-wrap: break-word;
    width: 250px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;

    position: absolute;
    z-index: 1;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}
